import xinput from '../../../wxcomponents/common/xinput.vue'
export default {
	data() {
		return {
			buttonTitle: '保存',
			input1:'',
			input2:'',
			input3:''
		}
	},
	methods: {
		bindDefault(e){
			this.input1 = e.inputValue
		},
		bindInput(e){
			this.input2 = e.inputValue
		},
		bindSecond(e){
			this.input3 = e.inputValue
		},
		
		onNext() {
			if(this.inpu1 == ''){
				uni.showToast({
					title: '请输入原密码',
					icon:'none'
				})
			} else if(this.input2 == ''){
				uni.showToast({
					title: '请输入新密码',
					icon:'none'
				})
			} else if(this.input3 == ''){
					uni.showToast({
						title: '请确认新密码',
						icon:'none'
					})
			} else{
				uni.showToast({
					title: '修改成功'
				})
				setTimeout(()=>{
					uni.reLaunch({
						url:'../../index/index'
					})
				},1000)
			}
		}
	},
	components:{
		xinput
	}
}
